$width: 100%;
$height: 100vh;
$bg-color-test: #000;
$font-weight: 600;
$text-color-test: #fff;
$text-center: center;
$font-roboto: "Roboto Mono", monospace;
$circle-border: 50%;
$white-bg: #fff;

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin posCenter {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

html,
body {
    height: $height;
    width: $width;
}

* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus {
    text-decoration: none;
}

.dark {
    position: relative;
    display: flex;
    width: 100%;
    height: 100vh;
    background: #121212;
    color: rgba(0, 0, 0, 0.87);
}

